.footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap:50px;
}
.footer-logo{
    width : 300px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap:10px;
    margin-top: 20px;
}
.footer-logo p{
    color : #383838;
    font-size: 50px;
    font-weight: 700;
    text-decoration: none;
}
.footer-details{
    padding : 8px 8px;
}
.footer hr{
    width : 80%;
    border : none;
    border-radius: 10px;
    height : 3px;
    background: #c7c7c7;
}

.footer-details tr{
    display: flex;
    list-style: none;
    gap: 60px;
    color : #252525;
    font-size: 30px;
}
.footer-details td{
    font-size: 18px;
}
.footer-social-icons{
    display: flex;
    gap:20px
}
.footer-icons-container{
    padding : 10px;
    padding-bottom: 6px;
    background: #fbfbfb;
    border : 1px solid #ebebeb;
}
.footer-copyright{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:30px;
    width : 100%;
    margin-bottom: 30px;
    color : #1a1a1a;
    font-size : 20px;
}
.footer-copyright hr{
    width : 80%;
    border : none;
    border-radius: 10px;
    height : 3px;
    background: #c7c7c7;
}