.productdisplay{
    display: flex;
    margin : 0px 170px;
}
.productdisplay-left{
    display: flex;
    gap: 17px;
}
.productdisplay-img-list{
    display: flex;
    flex-direction: column;
    gap : 16px;
}
.productdisplay-img-list img{
    height: 163px;
}
.productdisplay-main-img{
    width : 586px;
    height : 700px;
}
.productdisplay-right{
    margin : 0px 70px;
    display: flex;
    flex-direction: column;
}
.productdisplay-right h1{
    color :#3d3d3d;
    font-size: 40px;
    font-weight: 700;
}
.productdisplay-right-stars{
    display: flex;
    align-items: center;
    margin-top: 2px;
    gap : 5px;
    color :  #1c1c1c;
    font-size: 16px;
}
.productdisplay-right-prices{
    display: flex;
    margin : 15px 0px;
    gap : 30px;
    font-size: 20px;
    font-weight: 700;
}
.productdisplay-right-price-old{
    color : #818181;
    text-decoration: line-through;
}
.productdisplay-right-price-new{
    color : #ff4141;
}
.productdisplay-right-size h1{
    margin-top: 10px;
    color : #656565;
    font-size: 20px;
    font-weight: 600;
}
.productdisplay-right-sizes{
    display: flex;
    margin : 11px 0px;
    gap : 20px;
}
.productdisplay-right-sizes div{
    padding : 15px 22px;
    background: #fbfbfb;
    border : 1px solid #b0acac;
    border-radius: 3px;
    cursor: pointer;
    transform: scaleX(1);
    transition: transform 0.3s ease-in-out;
}

.productdisplay-right-sizes div:hover{
    transform: scaleX(1.2);
}
.productdisplay-right button{
    padding : 10px 20px;
    width : 200px;
    font-size: 16px;
    font-weight: 600;
    background: #ff4141;
    color : white;
    margin-top: 10px;
    margin-bottom: 15px;
    border : none;
    outline: none;
    cursor: pointer;
}

.productdisplay-right button:hover{
    background: #ec6464;
    color : white;
}
.productdisplay-right-category{
    margin-top: 10px;
}
.productdisplay-right-category span{
    font-weight: 600;
}